.followerCard
{
    background-color: rgb(61, 55, 55);
    box-shadow: 7px 4px 49px -12px rgba(56,51,51,1); 
    display: flex;
    align-items: flex-start;
    width: 300px;
    padding: 20px 30px;
    margin-right: 40px;
    margin-bottom: 40px;
    border-radius: 16px;
    transition: all 0.5s ease-in-out;
    cursor: pointer;
}
.followerCard:hover
{
    transform: scale(1.1);

}

.followerCard img
{
    border-radius: 50%;
    width: 75px;
    border: 5px solid white;
}
.followerCard h2
{
    color: white;
    font-size: 24px;
    padding-left: 12px;
}