.app__input
{
  text-align: center;
}
.app__input > h1
{
  padding: 20px 0;
}
.app__input input
{
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid grey;
  outline: none;
  padding: 10px 5px;
  width: 200px;
}
.app__input button
{
  background-color:#4C4C6D;
  color: white;
  border-radius: 5px;
  border: none;
  outline: none;
  margin-top: 10px;
  padding: 15px 30px;
  font-size: 18px;
  cursor: pointer;
  font-weight: bold;
}
nav
{
  background-color:#4C4C6D;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  margin-top: 30px;

}

nav ul
{
  list-style:  none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width:60%;
  padding: 16px 0;
}
nav ul li
{
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;
  transition: all 0.5s ease-in-out;
  /*color: rgb(19, 245, 19);*/
}
nav ul li:hover
{
  transform: scale(1.4);
} 
.navActive
{
  color: white;
}
.app__container
{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
  flex-wrap: wrap;
  margin-top: 20px;
}
.app__profile
{
  background-color: white;
  box-shadow: 7px 4px 49px -12px rgba(56,51,51,1); 
  width: 500px;
  text-align: center;
  padding: 20px;
  border-radius: 12px;
  margin-top: 150px;
  
}
.app__profile img
{
  width: 150px;
  margin: 5px;
  border-radius: 12px;
  margin-top: -100px;
  box-shadow: 7px 4px 49px -12px rgba(56,51,51,1); 
  margin-bottom: 20px;
}

.app__profile h3
{
  padding: 5px 0;
  font-size: 24px;
}
.app__profile p
{
  color: grey;
  font-size: 16px;
}