.repoCard
{
    background-color: rgb(61, 55, 55);
    box-shadow: 7px 4px 49px -12px rgba(56,51,51,1); 
    width: 300px;
    padding: 20px 30px;
    margin-right: 40px;
    margin-bottom: 40px;
    border-radius: 16px;
    transition: all 0.5s ease-in-out;
    cursor: pointer;
    color: white;
}
.repoCard:hover
{
    transform: scale(1.1);

}
.repoCard h2
{
    
    font-size: 24px;
    padding-left: 12px;
}
.repoCard ul
{
    padding-left: 30px;
    padding-top: 10px;
}
.repoCard li
{
    padding-bottom: 5px;
}